//         - media,  ,
// , , , @media-...
//     .

.media {
  display: flex;
  margin-top: $line-height;
  margin-bottom: $line-height;

  &__picture {
    flex-shrink: 0;
    margin-right: 1em;
  }

  &__body {
    flex-grow: 1;

    *:first-child {
      margin-top: 0;
    }
  }
}
